import { useForm } from "react-hook-form";
import styles from "./form.module.css";
import Image from "next/image";

type formvalue = {
  username: string;
  password: string;
};

export const Form = () => {
  const {
    handleSubmit,
    register,
    formState: { errors },
  } = useForm<formvalue>({
    mode: "onSubmit",
    delayError: 5,
  });

  const onSubmit = (values: formvalue) => {
    console.log(values.username);
  };

  return (
    <form onSubmit={handleSubmit(onSubmit)} className={styles.form}>
      <div className={styles.usernameContainer}>
        <Image
          src="/icons/username.png"
          className={styles.usernameLogo}
          alt="Username Logo"
          width={22}
          height={23}
        />
        <input
          {...register("username", {
            required: "Username can not be empty",
          })}
          type="text"
          placeholder="Username"
          className={styles.username}
        />
        <div className={styles.usernameErrCont}>
          {errors.username && (
            <p className={styles.usernameError}>{errors.username.message}</p>
          )}
        </div>
      </div>
      <div className={styles.passwordContainer}>
        <Image
          src="/icons/password.png"
          className={styles.passwordLogo}
          alt="Password Logo"
          width={23}
          height={24}
        />
        <input
          {...register("password", {
            required: "Password can not be empty",
          })}
          type="password"
          placeholder="Password"
          className={styles.password}
        />
      </div>
      <div className={styles.forgotPassCont}>
        <button className={styles.forgotPassBtn} type="button">
          Forgot password?
        </button>
      </div>
      <button type="submit" className={styles.loginButton}>
        Login
      </button>
    </form>
  );
};
